import React from 'react'
import { Card, Row, Col } from 'antd';
import { InputGroup, FormControl, Form, Button } from 'react-bootstrap';
import  '../css/worth.css'
import  '../css/w1.css'

class Prediction extends React.Component{
    constructor(){
        super();
        this.state={
            postcode: '',
            typee: '0',
            old: '0',
            duration: '0'
        }
        this.handleChange = this.handleChange.bind(this)
    }

    handleChange(event){
        const {name, value} = event.target
        this.setState({
            [name]: value
        })
    }

    render(){
        const DemoBox = props => <p className={`height-${props.value}`}>{props.children}</p>;
        return(
            <div>
                <h1>Predicting future house prices</h1>
                <div className="w1" style={{'textAlign':'left'}}>
                    <h2 style={{'paddingLeft':'20px', 'paddingTop':'5px'}}>Tell us about your property to get an instant estimate</h2>
                    {/* <h3 style={{'padding-left':'20px', 'padding-top':'5px'}}>Enter postcode</h3> */}
                            {/* <input name="postcode" value={this.state.postcode} ref={this.autocompleteInput}  id="autocomplete" placeholder="Enter your postcode"
                                type="text" onChange={this.handleChange} style={{width:'25%'}}></input> */}
                    <Row type="flex" justify="center" align="top">
                        <InputGroup className="mb-3" style={{'paddingLeft':'20px', 'paddingTop':'5px', width:'25%'}}>
                            <InputGroup.Prepend>
                                <InputGroup.Text id="inputGroup-sizing-default">Postcode</InputGroup.Text>
                            </InputGroup.Prepend>
                            <FormControl
                                aria-label="Default"
                                aria-describedby="inputGroup-sizing-default"
                                placeholder="e.g. SO16 3UJ"
                                name="postcode"
                                value={this.state.postcode}
                                onChange={this.handleChange}
                            />
                        </InputGroup>
                        <div style={{'paddingLeft':'20px', 'paddingTop':'5px', 'paddingBottom':'5px', width:'25%'}}>
                            <InputGroup className="mb-3">
                                <InputGroup.Prepend>
                                    <InputGroup.Text id="inputGroup-sizing-default">Property Type</InputGroup.Text>
                                </InputGroup.Prepend>
                                <Form.Control as="select" name="typee" value={this.state.typee} onChange={this.handleChange}>
                                    <option value='0'>Detached</option>
                                    <option value='1'>Semi-Detached</option>
                                    <option value='2'>Terraced</option>
                                    <option value='3'>Flats</option>
                                    <option value='4'>Other</option>
                                </Form.Control>
                            </InputGroup>
                        </div>
                        <div style={{'paddingLeft':'20px', 'paddingTop':'5px', 'paddingBottom':'5px', width:'25%'}}>
                            <InputGroup className="mb-3">
                                <InputGroup.Prepend>
                                    <InputGroup.Text id="inputGroup-sizing-default">Old/New</InputGroup.Text>
                                </InputGroup.Prepend>
                                <Form.Control as="select" name="old" value={this.state.old} onChange={this.handleChange}>
                                    <option value='0'>Old</option>
                                    <option value='1'>New</option>
                                </Form.Control>
                            </InputGroup>
                        </div>
                        <div style={{'paddingLeft':'20px', 'paddingTop':'5px', 'paddingBottom':'5px', width:'25%'}}>
                            <InputGroup className="mb-3">
                                <InputGroup.Prepend>
                                    <InputGroup.Text id="inputGroup-sizing-default">Duration</InputGroup.Text>
                                </InputGroup.Prepend>
                                <Form.Control as="select" name="duration" value={this.state.duration} onChange={this.handleChange}>
                                    <option value='0'>Freehold</option>
                                    <option value='1'>Leasehold</option>
                                </Form.Control>
                            </InputGroup>
                        </div>
                    </Row>
                    <div style={{'paddingLeft':'20px', 'paddingTop':'5px'}}>
                        <h5>Want to know what your home could be worth right now? Our Zoopla estimate will give you a guide price, based on analysis of the richest sources of property data in the UK.</h5>
                    </div>
                    <div style={{'paddingLeft':'20px', 'paddingTop':'5px', 'paddingBottom':'5px'}}>
                        <Button variant="warning"><a href={"/d_prediction/"+this.state.postcode+"/"+this.state.typee+"/"+this.state.old+"/"+this.state.duration}>Get Prediction</a></Button>
                    </div>
                </div>
                {/* <div className="title">
                    <span className="caption">
                        <span className="line line-l"></span>d
                        <span className="line line-r"></span>
                    </span>
                </div> */}
                <div>
                    <h1>Frequently asked questions</h1>
                    <Row>
                        <Col span={12}>
                            <div style={{ background: '#ECECEC', padding: '30px'}}>
                                <Card title="What if I just want to know how much my home is worth?" bordered={false} style={{ width: '100%' }}>
                                <p style={{"text_align": "center"}}>If you simply want a good indication of 
                                    your home's worth, a estimate is a great guide. 
                                    It's quick, free and uses some of the industry's most trusted sources, so it's pretty accurate to boot.</p>
                                </Card>
                            </div>
                        </Col>
                        <Col span={12}>
                            <div style={{ background: '#ECECEC', padding: '30px', height: '241px' }}>
                                <Card title="How is the estimate calculated?" bordered={false} style={{ width: '100%' }}>
                                <p>Our algorithm looks at information on all 28 million UK homes. We get our data from many sources including the government, 
                                    estate agents, and homeowners.</p>
                                </Card>
                            </div>
                        </Col>
                    </Row>
                    
                </div>
            </div>
        )
    }
}

export default Prediction