import React from 'react'
import { Row, Col } from 'antd';
import ConditionalForm from '../components/ConditionalForm'
import RangeMap from './RangeMap'
import Loading from '../icon/loading.gif'
import  '../css/savewait.css'
import axios from 'axios'

// const { Header, Footer, Sider, Content } = Layout;

class WhereView extends React.Component{
    constructor(){
        super()
        this.state={
            traffic: '',
            address: '',
            time: '',
            result: [],
            mouseId: '',
            itemIdd: '',
            height: '',
            width: ''
        }
        this.getChildData = this.getChildData.bind(this)
        this.getFinalResult = this.getFinalResult.bind(this)
        this.getMouseId = this.getMouseId.bind(this)
        this.getItemId = this.getItemId.bind(this)
    }

    getChildData(traffic,address, time){
        this.setState({
            traffic: traffic,
            address: address,
            time: time
        })
    }

    getFinalResult(res){
        console.log(res)
        this.setState({
            result: res
        })
    }

    getMouseId(mes){
        console.log(mes)
        this.setState({
            mouseId: mes
        })
    }

    getItemId(mes){
        console.log(mes)
        this.setState({
            itemIdd: mes
        })
    }

    getClick(mes){
        console.log(mes)
        if(mes === 'y'){
            document.getElementById('savewait').style.display = "block"
        }else if(mes === 'n'){
            document.getElementById('savewait').style.display = "none"
        }
    }

    getUrl(mes){
        console.log(mes)
        axios.get('http://127.0.0.1:8000/api/get_url/', {
            params: {
                url: mes
            }
        })
        .then(data => {
            // this.setState({
            //     result:Math.round(data.data).toLocaleString('en-US')
            // })
            console.log(data.data)
            var i = data.data.indexOf('?')
            var listing_id = data.data.slice(i-8,i)
            window.location.href = '/detail/'+listing_id
            
        })
    }

    // componentDidMount(){
    //     const height = document.getElementById('r').clientHeight;
    //     const width = document.getElementById('r').clientWidth;
    //     this.setState({ 
    //         height: height,
    //         width: width 
    //     });
    // }

    // shouldComponentUpdate(nextProps, nextState) {
    //     //   if (this.props.data != nextProps.data) {
    //     //       return true;
    //     //     }
    //     console.log(this.state.traffic)
    //     console.log(nextState.traffic)
    //         if (this.state.mouseId != nextState.mouseId) {
    //           return true
    //         }
    //         return true;
    //       }

    render(){
        console.log(this.state.result)
        console.log(this.state.mouseId)
        return(
            <div>
                <div id="savewait" name="savewait" className="savewait">
                    <img src={Loading} alt="" />
                </div>
                {/* <Layout>
                    <Layout>
                        <Sider style={{height:'100px', backgroundColor:'#F8F8FF'}}><ConditionalForm /></Sider>
                        <Content style={{height:'100px'}}>Content</Content>
                    </Layout>
                </Layout> */}
                <div id="r">
                <Row>
                    <Col span={10}>
                        <div style={{overflow: 'scroll', height:'600px'}}>
                        <ConditionalForm 
                            getChildData={this.getChildData}
                            result={this.state.result}
                            mouseId={this.state.mouseId}
                            itemId={this.getItemId}
                            getClick={this.getClick}
                            getUrl={this.getUrl}/>
                        </div>
                        
                    </Col>
                    <Col span={14}>
                        {/* {console.log(this.state.address)}
                        {console.log(this.state.traffic)}
                        {console.log(this.state.time)} */}
                        <RangeMap 
                            traffic={this.state.traffic}
                            address={this.state.address}
                            time={this.state.time}
                            check={"1"}
                            getFinalResult={this.getFinalResult}
                            result={this.state.result}
                            getMouseId={this.getMouseId}
                            itemIdd={this.state.itemIdd}
                            getClick={this.getClick}/>
                    </Col>
                </Row>
                </div>
            </div>
        )
    }
}

export default WhereView