import React, { PureComponent } from 'react'
import axios from 'axios'
import { Badge } from 'react-bootstrap';
import {
    ComposedChart, Line, Area, Bar, XAxis, YAxis, CartesianGrid, Tooltip,
    Legend,LineChart
  } from 'recharts';

class Detail_Prediction extends React.Component{
    constructor(){
        super();
        this.state = {
            result1: '',
            result2: '',
            result: '',
            type: '',
            duration: '',
            num: '',
            prices: ''
        }
    }

    componentDidMount(){
        // console.log(this.props.match.params.postcode)
        // console.log(this.props.match.params.typee)
        // console.log(this.props.match.params.old)
        // console.log(this.props.match.params.duration)

        axios.get('http://127.0.0.1:8000/api/prediction/', {
            params: {
                postcode: this.props.match.params.postcode,
                typee: this.props.match.params.typee,
                old: this.props.match.params.old,
                duration: this.props.match.params.duration,
            }
        })
        .then( res => {
            this.setState({
                // result: Math.round(res.data).toLocaleString('en-US')
                result1: res.data.split(",")[0],
                result2: res.data.split(",")[1],
                result: res.data.split(",")[2]
            })
        })

        var type
        if(this.props.match.params.typee == '0'){
            type='Detached'
        }else if(this.props.match.params.typee == '1'){
            type='Semi-Detached'
        }else if(this.props.match.params.typee == '2'){
            type='Terraced'
        }else if(this.props.match.params.typee == '3'){
            type='Flats'
        }else if(this.props.match.params.typee == '4'){
            type='Other'
        }

        var duration
        if(this.props.match.params.duration == '0'){
            duration='Freehold'
        }else if(this.props.match.params.duration == '1'){
            duration='Leasehold'
        }
        
        var pt = this.props.match.params.postcode
        var post = pt.slice(2,4)
        console.log(post)

        axios.get('http://127.0.0.1:8000/api/barchart/', {
            params:{
                postcode: post
            }
        })
        .then( res => {
            console.log(res.data[0].d)
            this.setState({
                num: res.data[0].d[0],
                prices: res.data[0].p[0]
            })
        })

        this.setState({
            type:type,
            duration: duration
        })

    }

    render(){
        const ColoredLine = ({ color }) => (
            <hr
                style={{
                    color: color,
                    backgroundColor: color,
                    height: 5
                }}
            />
        );

        console.log(this.state.num[0])
        console.log(this.state.prices[0])
        var data = []
        var name = ['January', 'February', 'March', 'April', 'May', 'June', 'July']
        for(var i=0;i<7;i++){
            var d = {'name':name[i], 'volume': parseFloat(this.state.num[i]), 'average price': Math.round(parseFloat(this.state.prices[i]))}
            data.push(d)
        }
        var d = {'name':'September(Prediction)', 'average price': Math.round(parseFloat(this.state.result))}
        data.push(d)
        console.log(data)

        // const data = [
        //     {
        //       name: 'Page A', uv: 4000, pv: 2400
        //     }
        //   ];

       

        return(
            <div style={{textAlign:'left'}}>
                <font>Property details for </font>
                <h1>{this.props.match.params.postcode}</h1>
                <Badge variant="secondary">{this.state.type}</Badge>&nbsp;&nbsp;&nbsp;&nbsp;
                <Badge variant="secondary">{this.state.duration}</Badge>
                <h3 style={{paddingTop:'80px'}}>Prediction</h3>
                <ColoredLine color="grey" />
                <font>Property value</font>
                <h1>￡{Math.round(this.state.result).toLocaleString('en-US')}</h1>
                <h5>
                    Range: 
                    {
                        this.state.result1 > this.state.result2 ?
                        <div>£{Math.round(this.state.result2).toLocaleString('en-US')} - £{Math.round(this.state.result1).toLocaleString('en-US')}</div> :
                        <div>£{Math.round(this.state.result1).toLocaleString('en-US')} - £{Math.round(this.state.result2).toLocaleString('en-US')}</div>
                    }
                </h5>
                <LineChart
                    width={1100}
                    height={300}
                    data={data}
                    margin={{
                    top: 10, right: 30, left: 20, bottom: 5,
                    }}
                >
                    <CartesianGrid strokeDasharray="3 3" />
                    <XAxis dataKey="name" />
                    <YAxis yAxisId="left" />
                    <YAxis yAxisId="right" orientation="right" />
                    <Tooltip />
                    <Legend />
                    <Line yAxisId="left" type="monotone" dataKey="volume" stroke="#8884d8" activeDot={{ r: 8 }} />
                    <Line yAxisId="right" type="monotone" dataKey="average price" stroke="#82ca9d" />
                </LineChart>

               
            </div>
        )
    }
}

export default Detail_Prediction