import '../config.js'
import React from 'react'
import HousesMap from './HousesMap'
import House from '../components/House'
import { Row, Col, Button } from 'antd';

class GeneralView extends React.Component{
    constructor(){
        super()
        this.state = {
            loading: false,
            data: [],
            house: [],
            loc: "",
            checkid: "",
            houseid: "",
            location: "",
            min_price: "-1",
            max_price: "-1",
            property_type: "-1",
            bedroom: "-1"
        }
        this.handleChange = this.handleChange.bind(this)
    }

    static defaultProps = {
        center: {
          lat: 59.95,
          lng: 30.33
        },
        zoom: 11
      };
    
    componentDidMount(){

        var location = this.props.match.params.location
        var min_price1 = this.props.match.params.min_price
        var max_price1 = this.props.match.params.max_price
        var property_type1 = this.props.match.params.property_type
        var bedroom1 = this.props.match.params.bedroom

        var min_price = ''
        var max_price = ''
        var property_type = ''
        var bedroom = ''

        // const min_price1
        // const max_price1
        // const property_type1
        // const bedroom1
        
        if(min_price1 == '-1'){
            min_price = ''
        }else{
            min_price = min_price1
        }

        if(max_price1 == '-1'){
            max_price = ''
        }else{
            max_price = max_price1
        }

        if(property_type1 == '-1'){
            property_type = ''
        }else{
            property_type = property_type1
        }

        if(bedroom1 == '-1'){
            bedroom = ''
        }else{
            bedroom = bedroom1
        }
        // console.log(`http://api.zoopla.co.uk/api/v1/property_listings.js?area=${location}&api_key=${global.key.key}&minimum_price=${min_price}&maximum_price=${max_price}&property_type=${property_type}&bedroom=${bedroom}`)
        
        fetch(`http://api.zoopla.co.uk/api/v1/property_listings.js?area=${location}&api_key=${global.key.key}&minimum_price=${min_price}&maximum_price=${max_price}&property_type=${property_type}&minimum_beds=${bedroom}`)
            .then(response => response.json())
            .then(data => {
                this.setState({
                    loading: true,
                    data: data.listing,
                    house: data,
                    location: location,
                    min_price: min_price1,
                    max_price: max_price1,
                    property_type: property_type1,
                    bedroom: bedroom1
                })
                console.log(data)
            })

    }

    onChildChange(loc){
        this.setState({
            loc: loc
        })
    }

    getChild(mes){
        console.log("first="+mes)
        this.setState({
            checkid: mes
        })
    }

    getHouseChild(mes){
        console.log("hosue?"+mes)
        this.setState({
            houseid: mes
        })
    }

    handleChange(event){
        const {name, value} = event.target
        this.setState({
            [name]: value
        })
    }
    
    render(){

        const price = []
        for(var i=1;i<=15;i++){
            price.push(i*10000)
        }

        const bedroom = []
        for(var i=1;i<=10;i++){
            bedroom.push(i+"+")
        }

        return(
            <div style={{ height: '150vh' }}>
                <div>
                    <Row>
                        <Col span={4}>Location</Col>
                        <Col span={4}>Min price</Col>
                        <Col span={4}>Max price</Col>
                        <Col span={4}>Property type</Col>
                        <Col span={4}>Bedrooms</Col>
                        <Col span={4}></Col>
                    </Row>
                    <Row>
                        <Col span={4}>
                            <input name="location" value={this.state.location} ref={this.autocompleteInput}  id="autocomplete" placeholder="Enter your address"
                                type="text" onChange={this.handleChange}></input>
                        </Col>
                        <Col span={4}>
                            <select name="min_price" value={this.state.min_price} style={{ width: 120 }} onChange={this.handleChange}>
                                <option value="-1">No min</option> 
                                {/* <option value="1">No</option>  */}
                                {/* {price} */}
                                {
                                    price.map( (d,i) => {
                                        // console.log(d.props.children)
                                        return <option key={i} value={d}>${d}</option>
                                    })
                                }
                            </select>
                        </Col>
                        <Col span={4}>
                            <select name="max_price" value={this.state.max_price} style={{ width: 120 }} onChange={this.handleChange}>
                                <option value="-1">No max</option>
                                {
                                    price.map( (d,i) => {
                                        // console.log(d.props.children)
                                        return <option key={i} value={d}>${d}</option>
                                    })
                                }
                            </select>
                        </Col>
                        <Col span={4}>
                            <select name="property_type" value={this.state.property_type} style={{ width: 120 }} onChange={this.handleChange}>
                                <option value="-1">show all</option>
                                <option value="houses">houses</option>
                                <option value="flats">flats</option> 
                            </select>
                        </Col>
                        <Col span={4}>
                            <select name="bedroom" value={this.state.bedroom} style={{ width: 120 }} onChange={this.handleChange}>
                                <option value="-1">no min</option>
                                {
                                    bedroom.map( (d,i) => {
                                        // console.log(d.props.children)
                                        return <option key={i} value={d}>{d}</option>
                                    })
                                }
                            </select>
                        </Col>
                        <Col span={4}>
                            <Button type="primary"><a href={'/generalview/'+this.state.location+'/'+this.state.min_price+'/'+this.state.max_price+'/'+this.state.property_type+'/'+this.state.bedroom}>Search</a></Button>
                        </Col>
                    </Row>
                </div>
                {/* <h2>{this.state.results}</h2> */}
                <h2>{this.props.match.params.location}</h2>
                {
                    // this.state.data.map(function(name){
                    //     return(
                    //         <div key={name.id}>
                    //             {name.latitude},{name.longitude}
                    //         </div>
                    //     )
                    // })
                }

                <div style={{ width: '50%', float: 'left'}}>
                    <House 
                        houses={this.state.house}
                        checked={this.state.checkid}
                        getHouseChild={msg=>this.getHouseChild(msg)}
                    />
                </div>

                <div style={{ height: '100vh', width: '50%', float: 'right'  }}>
                    <HousesMap
                        houses={this.state.data}
                        googleMapURL={`https://maps.googleapis.com/maps/api/js?key=AIzaSyA48E3YWd6V91lN0pdjaiemWejLEpje8bM&v=3.exp&libraries=geometry,drawing,places`}
                        loadingElement={<div style={{ height: `100%` }} />}
                        containerElement={<div style={{ height: `600px`, width: `650px` }} />}
                        mapElement={<div style={{ height: `100%` }} />}
                        getChild={msg=>this.getChild(msg)}
                        houseid={this.state.houseid}
                        alldata={this.state.house}
                    />
                </div>
                {/* <div>Icons made by <a href="https://www.freepik.com/" title="Freepik">Freepik</a> from <a href="https://www.flaticon.com/"                 title="Flaticon">www.flaticon.com</a> is licensed by <a href="http://creativecommons.org/licenses/by/3.0/"                 title="Creative Commons BY 3.0" target="_blank">CC 3.0 BY</a></div> */}
            
            </div>
        )
    }
}

export default GeneralView