import React from 'react'
import axios from 'axios'
import { Badge } from 'react-bootstrap';
import { Row, Col } from 'antd';

import { render } from 'react-dom'
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official'
import Pyramid from './Pyramid'

class Detail_Estimate extends React.Component{
    constructor(){
        super();
        this.state = {
            result: '',
            type: '',
            min_s: '',
            max_s: '',
            min_r: '',
            max_r: '',
            r_s: '',
            r_r: ''
        }
    }

    componentDidMount(){

        axios.get('http://127.0.0.1:8000/api/estimate/', {
            params: {
                postcode: this.props.match.params.postcode,
                typee: this.props.match.params.typee,
                bedroom: this.props.match.params.bedroom,
                bathroom: this.props.match.params.bathroom,
                floor: this.props.match.params.floor,
                reception: this.props.match.params.reception
            }
        })
        .then( res => {
            this.setState({
                // result: Math.round(res.data).toLocaleString('en-US')
                min_s: res.data.split(",")[0],
                max_s: res.data.split(",")[1],
                min_r: res.data.split(",")[2],
                max_r: res.data.split(",")[3],
                r_s: res.data.split(",")[4],
                r_r: res.data.split(",")[5],
            })
        })

        var type
        if(this.props.match.params.typee == '0'){
            type='Terraced house'
        }else if(this.props.match.params.typee == '1'){
            type='End terrace house'
        }else if(this.props.match.params.typee == '2'){
            type='Semi-detached house'
        }else if(this.props.match.params.typee == '3'){
            type='Detached house'
        }else if(this.props.match.params.typee == '4'){
            type='Mews house'
        }else if(this.props.match.params.typee == '5'){
            type='Flat'
        }else if(this.props.match.params.typee == '6'){
            type='Maisonette'
        }else if(this.props.match.params.typee == '7'){
            type='Detached bungalow'
        }
        
        this.setState({
            type:type
        })
    }

    pyramid(){
        return(
            <div>
                <Pyramid 
                        postcode={this.props.match.params.postcode}
                        price={this.state.r_s}/>
            </div>
        )
    }

    render(){
        const ColoredLine = ({ color }) => (
            <hr
                style={{
                    color: color,
                    backgroundColor: color,
                    height: 5
                }}
            />
        );

        

        return(
            <div style={{textAlign:'left'}}>
                <font>Property details for </font>
                <h1>{this.props.match.params.postcode}</h1>
                <Badge variant="secondary">{this.state.type}</Badge>&nbsp;&nbsp;&nbsp;&nbsp;
                <Badge variant="secondary">{this.props.match.params.bedroom}&nbsp;bedrooms</Badge>&nbsp;&nbsp;&nbsp;&nbsp;
                <Badge variant="secondary">{this.props.match.params.bathroom}&nbsp;bathrooms</Badge>&nbsp;&nbsp;&nbsp;&nbsp;
                <Badge variant="secondary">{this.props.match.params.floor}&nbsp;floors</Badge>&nbsp;&nbsp;&nbsp;&nbsp;
                <Badge variant="secondary">{this.props.match.params.reception}&nbsp;receptions</Badge>&nbsp;&nbsp;&nbsp;&nbsp;
                <h3 style={{paddingTop:'80px'}}>Estimate</h3>
                <ColoredLine color="grey" />
                <Row>
                    <Col span={12}>
                        <font>Property value</font>
                        <h1>￡{Math.round(this.state.r_s).toLocaleString('en-US')}</h1>
                    </Col>
                    <Col span={12}>
                        <font>Rental value</font>
                        <h1>￡{this.state.r_r}</h1>
                    </Col>
                </Row>
                <h5>
                    Range: 
                    
                        {/* // this.state.result1 > this.state.result2 ?
                        // <div>£{Math.round(this.state.result2).toLocaleString('en-US')} - £{Math.round(this.state.result1).toLocaleString('en-US')}</div> :
                        // <div>£{Math.round(this.state.result1).toLocaleString('en-US')} - £{Math.round(this.state.result2).toLocaleString('en-US')}</div> */}
                        <div>£{Math.round(this.state.min_s).toLocaleString('en-US')} - £{Math.round(this.state.max_s).toLocaleString('en-US')}</div>
                    
                </h5>

                {
                    this.state.r_s != 0?
                        this.pyramid():
                        console.log('0')
                }
            </div>
        )
    }
}

export default Detail_Estimate