import React from 'react'
import {Route} from 'react-router-dom'
import GeneralView from './containers/GeneralView'
import HomeView from './containers/HomeView'
import WhereView from './containers/WhereView'
import Detail from './containers/Detail'
import Login from './containers/Login'
import Signup from './containers/Signup'
import Prediction from './containers/Prediction'
import Worth from './containers/Prediction'
import D_Prediction from './containers/Detail_Prediction'
import Estimate from './containers/Estimate'
import D_Estimate from './containers/Detail_Estimate'
import Profile from './containers/Profile'


const BaseRouter = () => (
    <div>
        <Route exact path='/' component={HomeView} />
        <Route exact path='/generalview/:location/:min_price/:max_price/:property_type/:bedroom' component={GeneralView}/>
        <Route exact path='/d_prediction/:postcode/:typee/:old/:duration' component={D_Prediction}/>
        <Route exact path='/d_estimate/:postcode/:typee/:bedroom/:bathroom/:floor/:reception' component={D_Estimate}/>
        <Route exact path='/whereview/' component={WhereView} />
        <Route exact path='/detail/:id/' component={Detail} />
        <Route exact path='/login/' component={Login} />
        <Route exact path='/signup/' component={Signup} />
        <Route exact path='/prediction/' component={Prediction} />
        <Route exact path='/estimate/' component={Estimate} />
        <Route exact path='/profile/' component={Profile} />
        {/* <Route exact path='/worth/' component={Worth} /> */}
    </div>
);

export default BaseRouter