import '../config.js'
import React from 'react'
import axios from 'axios';
import '../css/ant.css'
import { Row, Col, Button } from 'antd';
import LoginForm from '../components/LoginForm';
import SignupForm from '../components/SignupForm';
import ReadMore from './ReadMore';
import { Carousel } from 'react-bootstrap';
import { browserHistory } from 'react-router';


class Detail extends React.Component{
    constructor(props){
        super(props)
        this.state={
            data: [],
            house: [],  
            img: [],
            check: '',
            logged_in: localStorage.getItem('token') ? true : false,
            username: '',
            check_save: '',
            u:''
        }
        this.buildImg = this.buildImg.bind(this)
        this.handleSave = this.handleSave.bind(this)
    }


    handle_login = (e, data) => {
        e.preventDefault();
        fetch('http://localhost:8000/token-auth/', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(data)
        })
          .then(res => res.json())
          .then(json => {
            console.log(json.token === 'undefined')
            localStorage.setItem('token', json.token);
            if(typeof json.token !== 'undefined'){
              this.setState({
                logged_in: true,
                displayed_form: '',
                username: json.user.username,
                check: "Hello, "+json.user.username
              });
            }else{
              this.setState({
                check: "Sorry, Invalid credentials!"
              })
            }
            
          });
      };

    handleSave(){
        console.log(localStorage.getItem('username'))
        if(localStorage.getItem('username') == null){
            // window.location.href = '/login'
            this.props.history.push("/login");
        }
        console.log(this.state.logged_in)
        axios.get('http://127.0.0.1:8000/api/save/', {
            params: {
                username: localStorage.getItem('username'),
                listing_id: this.state.data.listing_id
            }
        })
        .then(res => {
            if(res.data == 'ok'){
               this.setState({
                    check_save: 'y'
               })
            }
        })
        // .then(data => {
        //     console.log(this.props.location.pathname)
        //     this.props.history.push(this.props.location.pathname);
        // })
    }

    save_delete(value){
        console.log(value)
            axios.get('http://127.0.0.1:8000/api/delete_save/', {
                params: {
                    username: localStorage.getItem('username'),
                    listing_id: value
                }
            })
            .then(res => {
                if(res.data == 'ok'){
                   this.setState({
                        check_save: 'n'
                   })
                }
            })
            .then(data => {
                this.setState({
                    u:'e'
                })
            })
    }

    shouldComponentUpdate(nextProps, nextState) {
        console.log(this.state.u)
        console.log(nextState.u)
        // if (this.state.u !== nextState.u) {
        //     return true;
        //   }
        //   return true;
    }

    componentDidMount(){
        fetch(`http://api.zoopla.co.uk/api/v1/property_listings.js?listing_id=${this.props.match.params.id}&api_key=${global.key.key}`)
            .then(response => response.json())
            .then(data => {
                data.listing.map( d => 
                    axios.get(`http://127.0.0.1:8000/api/img/`, {
                        params: {
                            p: d.details_url
                          }
                    })
                    .then(res => {
                        // this.setState({
                        //     articles: res.data
                        // });
                        //get img url
                        // console.log(res.data);
                        this.setState({
                            img: res.data.split(";")
                        })
                    }))
            })

        fetch(`http://api.zoopla.co.uk/api/v1/property_listings.js?listing_id=${this.props.match.params.id}&api_key=${global.key.key}`)
            .then(response => response.json())
            .then(data => {
                data.listing.map(d => {
                    this.setState({
                        data: d
                    })
                    console.log(d)
                })
                console.log(data.listing)
            })

        //check if this user have this property
        fetch(`http://api.zoopla.co.uk/api/v1/property_listings.js?listing_id=${this.props.match.params.id}&api_key=${global.key.key}`)
            .then(response => response.json())
            .then(data => {
                data.listing.map(d => {
                    axios.get('http://127.0.0.1:8000/api/check_save/', {
                        params: {
                            username: localStorage.getItem('username'),
                            listing_id: d.listing_id
                        }
                    })
                    .then(data => {
                        this.setState({
                            check_save: data.data
                        })
                    })
                })
            })
    }

    buildImg(){
        console.log(this.state.img[i])
        for(var i=0;i<this.state.img.length;i++){
           return(
            <Carousel.Item key={i}>
                <img
                className="d-block w-100"
                src={this.state.img[i]}
                alt="First slide"
                />
                <Carousel.Caption>
                <h3>First slide label</h3>
                <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                </Carousel.Caption>
            </Carousel.Item>
           )                 
        }
    }

    shouldComponentUpdate(nextProps, nextState) {
        // if (this.props.color !== nextProps.color) {
        //   return true;
        // }
        if (this.state.u === nextState.u) {
          return true;
        }
        return true;
      }

    render(){
        const DemoBox = props => <p className={`height-${props.value}`}>{props.children}</p>;
        const d = []
        for(var i=0;i<this.state.img.length;i++){
            // d.push(<div key={i} style={{textAlign: 'center'}}><img src={this.state.img[i]} /></div>)
            d.push(
                    <Carousel.Item key={i}>
                        <div key={i} style={{width: '100%', height: '500px', backgroundColor: '#BCBCBC'}}>
                            <img
                                className="d-block w-100"
                                src={this.state.img[i]}
                                style={{width: 'auto',
                                    height: 'auto',
                                    maxWidth: 'none',
                                    maxHeight: '100%',
                                    objectFit: 'none',
                                    objectPosition: 'center'}}
                                alt="First slide"
                            />
                        </div>
                    {/* <Carousel.Caption>
                    <h3>First slide label</h3>
                    <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                    </Carousel.Caption> */}
                    </Carousel.Item>
            )
        }

        let form;
        switch (this.state.check) {
          case 'login':
            console.log('login1')
            form = <LoginForm handle_login={this.handle_login} />;
            break;
          case 'signup':
              console.log('signup1')
            form = <SignupForm handle_signup={this.handle_signup} />;
            break;
          default:
            form = null;
        }
        return(
            <div>
                <div>
                    {form}
                </div>
                <div>
                    {/* {this.props.match.params.id} */}
                    {/* <Carousel effect="fade">
                        {d}
                    </Carousel> */}
                    <Carousel>
                        {d}
                        {/* <Carousel.Item>
                            <img
                            className="d-block w-100"
                            src="holder.js/800x400?text=First slide&bg=373940"
                            alt="First slide"
                            />
                            <Carousel.Caption>
                            <h3>First slide label</h3>
                            <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                            </Carousel.Caption>
                        </Carousel.Item> */}
                    </Carousel>
                </div>
                <div>
                <Row>
                    <Col span={12}>
                        <Row type="flex" justify="center" align="top" style={{paddingBottom: '50px'}}>
                            <Col span={6}>
                                    <DemoBox value={50}>
                                        {this.state.data.num_bathrooms} bathrooms
                                    </DemoBox>
                            </Col>
                            <Col span={6}>
                                    <DemoBox value={50}>
                                        {this.state.data.num_bedrooms} bedrooms
                                    </DemoBox>
                            </Col>
                            <Col span={6}>
                                    <DemoBox value={50}>
                                        {this.state.data.num_floors} floors
                                    </DemoBox>
                            </Col>
                            <Col span={6}>
                                    <DemoBox value={50}>
                                        {this.state.data.num_recepts} recepts
                                    </DemoBox>
                            </Col>
                        </Row>
                        <Row type="flex" justify="center" align="top">
                            <h1>Description</h1>
                            <ReadMore text={this.state.data}/>
                            {/* {this.state.data.description} */}
                        </Row>
                    </Col>
                    <Col span={12}>
                        <h1>
                            {
                                this.state.data.displayable_address
                            }
                        </h1>
                        <h1>
                            £
                        <font style={{color: 'blue'}}>
                        {
                            this.state.data.price
                        }
                        </font>
                        </h1>
                        <br />
                        <Row type="flex" justify="center" align="top">
                            <Col span={12}>
                                <DemoBox value={100}>
                                    <img src={this.state.data.agent_logo}></img>
                                </DemoBox>
                            </Col>
                            <Col span={12}>
                                <DemoBox value={50}>
                                    {this.state.data.agent_name}
                                </DemoBox>
                                <DemoBox value={50}>
                                    {this.state.data.agent_phone}
                                </DemoBox>
                            </Col>
                        </Row>
                            {
                                this.state.check_save === 'y'?

                                <Button type="danger" onClick={() => {this.save_delete(this.state.data.listing_id)}}>Unsave</Button>

                                :

                                <Button type="primary" onClick={this.handleSave}>Save</Button>
                            }
                        
                    </Col>
                </Row>
                </div>

                
            </div>
        )
    }
}

export default Detail