import React from 'react'
import { withScriptjs, withGoogleMap, GoogleMap } from "react-google-maps";
import HouseMarker from './HouseMarker'

var central = { lat:  50.785113, lng:  -1.591953}
const HousesMap = withScriptjs(withGoogleMap((props) =>{
    // const lat = props.alldata.latitude
    console.log(props.houses)
    // const lng = props.alldata.longitude
    const houseid = props.houseid
    const markers = props.houses.map( house => <HouseMarker
                      key={house.listing_id}
                      house={house}
                      location={{lat: house.latitude, lng: house.longitude}}
                      idd={house.listing_id}
                      // fn={getChild}
                      getChild={msg=>getChild(msg)}
                      houseid={houseid}
                    />);
                    // console.log(props.houses["0"].latitude)  
                    
    function getChild(msg){
      console.log("second="+msg)
      props.getChild(msg)
    }

    

    return (
        <GoogleMap
          defaultZoom={10}
          center={ central }
          >
          {markers}
        </GoogleMap>
      );
    }
  ))
export default HousesMap