import React from 'react'
import RadiusMap from './RadiusMap'

class RangeMap extends React.Component{
  constructor(){
    super()
    this.state = {
      startingLocation : "",
      departureTime : new Date().toJSON(),
      travelTime: 15 * 60,
      APPLICATION_ID: "63e341d5",
      API_KEY: "7cfd94d9ef4133cb8af7fa7ff522f953",
      data: [],
      location: "",
      type: "",
      address: "",
      latlng: [],
      check: "",
      finalResult: []
    }
    this.getLatlng = this.getLatlng.bind(this)
    this.getMouse = this.getMouse.bind(this)
  }

  getLatlng(mes, res){
    console.log(mes)
    console.log(res)
    this.setState({
      latlng: mes,
      check: "2",
      finalResult: res
    })
    this.props.getFinalResult(res)
  }

  getDate(address, traffic, time){
    console.log(traffic)
    console.log(time)
    console.log(address)
    //  if(this.props.address != ''){
      fetch(`https://api.traveltimeapp.com/v4/geocoding/search?query=${address}`,{
        method: "GET",
        // body: JSON.stringify(request),
        headers: {
            "X-Application-Id": "63e341d5",
            "X-Api-Key": "7cfd94d9ef4133cb8af7fa7ff522f953"
        },
        })
        .then(response => response.json())
        .then(data => {
        console.log(data.features[0].geometry.coordinates[1])
        var latLng = {lat: data.features[0].geometry.coordinates[1], lng: data.features[0].geometry.coordinates[0]}
        this.setState({
            address: address,
            type: traffic,
            time: time,
            location: latLng,
            check: "",
            mes:''
        })
        this.getRange(latLng,traffic, time)
        })
    //  }
    }

    getRange(latLng,traffic, time){
        var request = {
            departure_searches: [{
            id: "first_location",
            coords: latLng,
            transportation: {
                type: traffic },

            departure_time: this.state.departureTime,
            travel_time: time * this.state.travelTime }],

            arrival_searches: [] };

            fetch(`https://api.traveltimeapp.com/v4/time-map`,{
            method: "POST",
            body: JSON.stringify(request),
            headers: {
                "X-Application-Id": "63e341d5",
                "X-Api-Key": "7cfd94d9ef4133cb8af7fa7ff522f953",
                "Content-Type": "application/json; charset=UTF-8"
            },
            })
            .then(response => response.json())
            .then(data => {
            console.log(data.results[0].shapes)
                this.setState({
                data: data.results[0].shapes
                })
            })
    }

    getMouse(mes){
      console.log(mes)
      console.log(this.props.traffic)
      this.setState({
        mes: mes
      })
      this.props.getMouseId(this.state.mes)
      // console.log(this.props.getMouseId(mes))
    }

  // shouldComponentUpdate(nextProps, nextState) {
  //   // if (this.props.data != nextProps.data) {
  //   //     return false;
  //   //   }
  //   console.log(this.state.latlng)
  //   console.log(nextState.latlng)
  //     if (this.state.latlng === nextState.latlng) {
  //       return true
  //     }
  //     return true;
  //   }

  render(){
    if(this.props.address !== this.state.address ){
      this.getDate(this.props.address, this.props.traffic, this.props.time)
    }

    console.log(this.state.mes)

    // if(this.state.mes !== ''){
    //     this.props.getMouseId(this.state.mes)
    //     // this.send(this.state.mes)
    // }

    // if(this.props.check != this.state.check){
    //   this.getDate(this.props.address, this.props.traffic, this.props.time, "1")
    // }
    
    return(
      <div>
        <RadiusMap
            location={this.state.location}
            data={this.state.data}//shape
            address={this.state.address}
            latlng={this.state.latlng}
            getLatlng={this.getLatlng}
            check={this.state.check}
            result={this.props.result}
            getMouse={this.getMouse}
            getClick={this.props.getClick}
            googleMapURL={`https://maps.googleapis.com/maps/api/js?key=AIzaSyA48E3YWd6V91lN0pdjaiemWejLEpje8bM&v=3.exp&libraries=geometry,drawing,places`}
            loadingElement={<div style={{ height: `100%` }} />}
            containerElement={<div style={{ height: `600px`, width: `650px` }} />}
            mapElement={<div style={{ height: `100%` }} />}
            itemIdd={this.props.itemIdd}
        />
      </div>
    )
  }
}

export default RangeMap


