import React from 'react'
import { Marker } from "react-google-maps";
import Icon1 from '../icon/home2.png'
import Icon2 from '../icon/home-address.png'

class HouseMarker extends React.Component{
    constructor(){
        super()
        this.state = {
            icon: 'Icon1'
        }
        this.handle = this.handle.bind(this)
    }

    handle(event){
        console.log(event)
        this.props.getChild(event)
    }
    
    change(){
        console.log(this.props.location)
        if(this.props.houseid === this.props.idd){
            return(
                <Marker
                    position={this.props.location}
                    icon={Icon2}
                    onMouseOver={this.handle.bind(this,this.props.idd)}
                    onMouseOut={this.handle.bind(this,"leave")}
                    // onMouseOver={this.props.getChild('dfsdf')}
                    >
                </Marker>
            )
        }else{
            return(
                <Marker
                    position={this.props.location}
                    icon={Icon1}
                    onMouseOver={this.handle.bind(this,this.props.idd)}
                    onMouseOut={this.handle.bind(this,"leave")}
                    // onMouseOver={this.props.getChild('dfsdf')}
                    >
                </Marker>
            )
        }
    }

    render(){
        return(
            // console.log("final="+this.props.houseid),
            // <Marker
            //     position={this.props.location}
            //     icon={this.state.icon}
            //     onMouseOver={this.handle.bind(this,this.props.idd)}
            //     onMouseOut={this.handle.bind(this,"leave")}
            //     // onMouseOver={this.props.getChild('dfsdf')}
            //     >
            // </Marker>
            this.change()
        )
    }
}

export default HouseMarker