import { Layout, Menu} from 'antd';
import React from 'react';
import { Link, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import * as actions from '../store/actions/auth';

const { Header, Content, Footer } = Layout;

class CustomLayout extends React.Component {
    constructor(){
      super()
      this.state={
        username: '',
        check: '',
        r:''
      }
    }

    render(){
      
        return(
            <div>
                <Layout className="layout">
                <Header>
                <div className="logo" />
                {/* {this.props.logged_in ? this.logged_in_nav : this.logged_out_nav} */}
                <Menu
                    theme="dark"
                    mode="horizontal"
                    defaultSelectedKeys={['']}
                    style={{ lineHeight: '64px' }}
                >

                    
                    <Menu.Item key="6"><Link to='/'>Search</Link></Menu.Item>
                    {/* <Menu.Item key="3">Mark trends</Menu.Item> */}
                    <Menu.Item key="4"><Link to='/whereview'>Where can I live</Link></Menu.Item>
                    <Menu.Item key="8"><Link to='/prediction'>Predicting House Prices</Link></Menu.Item>
                    <Menu.Item key="9"><Link to='/estimate'>How much is my home worth?</Link></Menu.Item>
                    <Menu.Item key="5">{this.state.check}</Menu.Item>
                    <Menu.Item key="7"><Link to='/profile'>{localStorage.getItem('username')}</Link></Menu.Item>
                    {
                        this.props.isAuthenticated ?
        
                        <Menu.Item key="2" onClick={this.props.logout}>
                            Logout
                        </Menu.Item>
        
                        :
        
                        <Menu.Item key="2">
                            {/* <Link to="/login">Login</Link> */}
                            <Link to={{
                                pathname:"/login",
                                state:{}
                            }}  className="name-load">Login</Link>
                        </Menu.Item>
                    }
                </Menu>
                
                    
                </Header>
                <Content style={{ padding: '0 50px' }}>
                    <div style={{ background: '#fff', padding: 24, minHeight: 550 }}>
                                {this.props.children}
                            </div>
                </Content>
                {/* <TT /> */}
                <Footer style={{ textAlign: 'center' }}>Ant Design ©2018 Created by Ant UED</Footer>
                
<img src="https://www.zoopla.co.uk/static/images/mashery/powered-by-zoopla-150x73.png" width="150" height="73" title="Property information powered by Zoopla" alt="Property information powered by Zoopla" border="0"></img>
            </Layout>
            </div>
        );
    }
}

const mapDispatchToProps = dispatch => {
  return {
      logout: () => dispatch(actions.logout()) 
  }
}

export default withRouter(connect(null, mapDispatchToProps)(CustomLayout));
  