import React from 'react'
import createG2 from 'g2-react';
import axios from 'axios'

class Pyramid extends React.Component{
    constructor(){
        super()
        this.state={
            data: [
                {"action": "Website visits", "value": 5654,"value1": 5654},
                {"action": "Downloads", "value": 4064,"value1": 5654},
                {"action": "Requested price list", "value": 1987,"value1": 5654},
                {"action": "Invoice sent", "value": 976,"value1": 5654},
                {"action": "Finalized", "value": 484,"value1": 5654}
            ],
            forceFit: true,
            width: 500,
            height: 450,
            plotCfg: {
                margin: 80
            },
            color: ['#BBBBBB', '#BBBBBB', '#FF0000', '#BBBBBB', '#BBBBBB']
        }
    }

    componentDidMount(){
        console.log(this.props.price)
        axios.get('http://127.0.0.1:8000/api/pyramid/', {
            params:{
                postcode: this.props.postcode,
                price: Math.round(this.props.price)
            }
        })
        .then( res => {
            console.log(res.data)
            this.setState({
                data: res.data,
                color: res.data[0].color
            })

        })
    }

    render(){
        const Chart = createG2(chart => {
            chart.coord('rect').transpose().scale(1,-1);
            chart.axis(false);
            chart.legend('action', {
              position: 'bottom'
            });
            chart.intervalSymmetric()
              .position('action*value')
              .color('action', this.state.color)
              .shape('funnel')
              .label('action', {offset: 10, label: {fontSize: 14}});
            chart.render();
          });

        return(
            <div>
                <Chart
                    data={this.state.data}
                    width={this.state.width}
                    height={this.state.height}
                    plotCfg={this.state.plotCfg}
                    forceFit={this.state.forceFit} />
            </div>
        )
    }
}


